import React, { useState } from 'react';
import './SurpriseModal.css';

function SurpriseModal() {
  const [showModal, setShowModal] = useState(false);

  const toggleModal = () => setShowModal(!showModal);

  return (
    <>
      <button className="surprise-btn" onClick={toggleModal}>
        <span role="img" aria-label="gift">🎁</span> Apke liye Surprise!
        <span role="img" aria-label="party">🎉</span> Surprise! 🎉
      </button>

      {showModal && (
        <div className="modal">
          <div className="modal-content">
            <span className="close-btn" onClick={toggleModal}>&times;</span>
            <h2><span role="img" aria-label="party">🎉</span> Surprise! <span role="img" aria-label="party">🎉</span></h2>
            <p>Aap Duniya me sabse best ho Vasu ji, and I’m so lucky to have you in my life. Let's enjoy this special day! 💖</p>
            {/* Add the image below the message */}
            <img src="/images/surprise.jpg" alt="Surprise" className="surprise-image" />
          </div>
        </div>
      )}
    </>
  );
}

export default SurpriseModal;
